<template>
    <a-spin :spinning="spinning">
        <div class="login">
            <div class="login-content">
                <h4>{{$t('login.title')}}</h4>
                <div class="login-language">
                    <a-button
                            v-for="entry in languages"
                            :key="entry.language"
                            @click="changeLocale(entry.language)"
                            class="language-btn"
                            :class="{'active' : chosenLanguage === entry.language}"
                    >
                        <flag :iso="entry.flag" v-bind:squared=false />
                    </a-button>
                </div>
                <div class="login-form">
                    <div class="input-wrapper" :class="{'errorInput' : errors.username}">
                        <label for="username">{{$t('login.username')}} <span>*</span></label>
                        <a-input id="username" v-model.trim="username" autoFocus />
                        <div class="error" v-if="errors.username">{{$t('login.noUser')}}</div>
                    </div>
                    <div class="input-wrapper" :class="{'errorInput' : errors.password}">
                        <label for="password">{{$t('login.password')}} <span>*</span></label>
                        <a-input-password id="password" v-model.trim="password" />
                        <div class="error" v-if="errors.password">{{$t('login.noPass')}}</div>
                    </div>
                    <div class="error" v-if="errors.wrong">{{$t('login.wrong')}}</div>
                    <div class="button">
                        <a-button
                                type="submit"
                                class="custom-button"
                                @click="handleSubmit"
                        >
                            {{$t('login.submit')}}
                        </a-button>
                    </div>
                </div>
            </div>
        </div>
    </a-spin>
</template>

<script>
    import i18n from "../../plugins/i18n";
    import links from "../../router/links";
    import data from "../../constant";

    export default {
        name: "Login",
        data() {
            return {
                errors: {username: '', password: '', wrong: ''},
                username: '',
                password: '',
                languages: [
                    { flag: 'us', language: 'en' },
                    { flag: 'vn', language: 'vi' }
                ],
                chosenLanguage: i18n.locale,
            }
        },
        watch: {
            username() {
                if (this.errors.username) {
                    delete this.errors.username;
                }
            },
            password() {
                if (this.errors.password) {
                    delete this.errors.password;
                }
            }
        },
        computed: {
            spinning() {
                return this.$store.state.spinning;
            }
        },
        methods: {
            handleCheck() {
                let check = false;
                const user = data.user;
                if (!this.username) {
                    this.errors.username = i18n.t('login.noUser');
                    check = true;
                }
                if (!this.password) {
                    this.errors.password = i18n.t('login.noPass');
                    check = true;
                }
                if (this.username && this.password) {
                    if (this.username !== user.username || this.password !== user.password) {
                        this.errors.wrong = i18n.t('login.wrong');
                        check = true;
                    } else {
                        delete this.errors.wrong;
                        check = false;
                    }
                }
                return check;
            },
            async handleSubmit() {
                if (!this.handleCheck()) {
                    await this.$store.dispatch('setSpinning', true);
                    const data = {
                        username: this.username,
                        password: this.password,
                    };
                    setTimeout(() => {
                        this.$store.dispatch('saveUser', data);
                        this.$router.push(links.HOME);
                        this.$store.dispatch('setSpinning', false);
                    }, 2000);
                }
            },
            changeLocale(locale) {
                if (i18n.locale !== locale) {
                    i18n.locale = locale;
                    this.chosenLanguage = locale;
                    this.$store.dispatch('setLanguage', locale);
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    @import "../../assets/scss/variables";
    @keyframes topToDown {
        0% {
            margin: auto;
            width: 0;
            opacity: 0;
            visibility: hidden;
            transform: translateY(-100px);
        }
        33% {
            margin: auto;
            width: 0;
            opacity: 0.8;
            visibility: visible;
            transform: translateY(0px);
        }
        100% {
            margin: auto;
            width: 350px;
            opacity: 1;
            visibility: visible;
        }
    }
    @keyframes show {
        0% {
            opacity: 0;
            visibility: hidden;
        }
        60% {
            opacity: 0.1;
            visibility: visible;
        }
        100% {
            opacity: 1;
            visibility: visible;
        }
    }
    .login {
        background: $gradient;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        .language-btn {
            background: transparent;
            border: none;
            height: auto;
            padding: 0 3px;
            filter: grayscale(1);
            &.active, &:hover {
                filter: grayscale(0);
            }
        }
        &-content {
            background: rgba(255,255,255,0.15);
            padding: 1rem 2rem;
            border-radius: 2px;
            box-shadow: 0 0 5px rgb(255 255 255 / 50%);
            position: relative;
            overflow: hidden;
            width: 350px;
            animation: topToDown 1.5s ease-out;
        }
        h4 {
            text-transform: uppercase;
            color: #fff;
            font-weight: 700;
            font-size: 1.2rem;
            letter-spacing: 0.1px;
            text-align: center;
            margin: 0.5rem 0;
            animation: show 1.5s ease-out;
            white-space: nowrap;
        }
        &-language {
            position: absolute;
            top: 5px;
            right: 5px;
            animation: show 1.5s ease-out;
        }
        &-form {
            animation: show 1.5s ease-out;
            .button {
                text-align: center;
                margin-top: 10px;
            }
        }
        .error {
            color: #fff!important;
        }
    }
    .input-wrapper {
        margin-bottom: 10px;
        label {
            font-size: 0.9rem;
            margin-bottom: 3px;
            display: block;
            color: #fff;
            span {
                color: #ff0000;
            }
        }
        input {
            border: 1px solid #fff;
            border-radius: 2px;
            background: #fff;
            box-shadow: none!important;
        }
        &.errorInput {
            input, textarea, & > span {
                border-color: #ff0000!important;
                &:focus {
                    box-shadow: none!important;
                }
            }
        }
    }
</style>